:root {
  --theme-base: "black-highlighter";
  --theme-id: "black-highlighter";
  --theme-name: "Black Highlighter Theme";
  --logo-image: url("../img/logo.svg");
  --header-title: "SCP Foundation";
  --header-subtitle: "SECURE, CONTAIN, PROTECT";
  --body-font: Inter, Verdana, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
  --UI-font: PTRootUI, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantrell, "Helvetica Neue", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --header-font: Inter, "Franklin Gothic Medium", "Franklin Gothic", "ITC Franklin Gothic", "Helvetica Neue", Helvetica, Arial, sans-serif;
  --title-font: var(--header-font);
  --mono-font: Recursive, Consolas, Monaco, monospace;
  --min-font-size: .8125rem;
  --max-font-size: .9375rem;
  --base-font-size: max(var(--min-font-size), min(2vw, var(--max-font-size)));
  --base-line-height: 1.5;
  --header-h1-font-size: calc(var(--base-font-size) * (40 / 15));
  --header-h2-font-size: var(--base-font-size);
}

@media only screen and (max-width: 56.25rem) {
  :root {
    --base-font-size: var(--min-font-size);
  }
}

:root {
  --wght: 400;
  --ui-wght: 500;
  --ui-hvr-wght: 500;
  --ital: 0;
  --mono-wght: 350;
  --slnt: 0;
  --CASL: 0;
  --CRSV: 0;
  --MONO: 1;
  --lnum: 1;
  --pnum: 1;
  --kern: 1;
  --cpsp: 1;
  --liga: 1;
  --case: 1;
  --clig: 1;
  --dlig: 0;
  --calt: 1;
  --aalt: 0;
  --ss01: 1;
  --ss02: 0;
  --ss03: 1;
  --ss04: 1;
  --zero: 1;
  --cv11: 0;
  --white-monochrome: 252, 252, 252;
  --pale-gray-monochrome: 244, 244, 244;
  --light-pale-gray-monochrome: 244, 244, 244;
  --very-light-gray-monochrome: 215, 215, 215;
  --light-gray-monochrome: 160, 160, 160;
  --gray-monochrome: 66, 66, 72;
  --dark-gray-monochrome: 48, 48, 52;
  --black-monochrome: 12, 12, 12;
  --pale-accent: 210, 20, 20;
  --bright-accent: 133, 0, 5;
  --medium-accent: 100, 46, 44;
  --dark-accent: 100, 3, 15;
  --alt-accent: 221, 102, 17;
  --swatch-primary: var(--bright-accent);
  --swatch-primary-darker: var(--medium-accent);
  --swatch-primary-darkest: var(--dark-accent);
  --swatch-border-color: var(--black-monochrome);
  --swatch-text-dark: var(--black-monochrome);
  --swatch-text-light: var(--white-monochrome);
