@-webkit-keyframes bhl-fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes bhl-fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes bhl-slide-in {
  from {
    translate: 1rem 1rem;
  }

  to {
    translate: 0;
  }
}

@keyframes bhl-slide-in {
  from {
    translate: 1rem 1rem;
  }

  to {
    translate: 0;
  }
}

@supports not (translate: 1rem 1rem) {
  @-webkit-keyframes bhl-slide-in {
    from {
      -webkit-transform: translate(1rem, 1rem);
      transform: translate(1rem, 1rem);
    }

    to {
      -webkit-transform: translate(0);
      transform: translate(0);
    }
  }

  @keyframes bhl-slide-in {
    from {
      -webkit-transform: translate(1rem, 1rem);
      transform: translate(1rem, 1rem);
    }

    to {
      -webkit-transform: translate(0);
      transform: translate(0);
    }
  }
}

@-webkit-keyframes bhl-slide-down {
  from {
    max-height: 0;
  }

  to {
    max-height: var(--header-height-on-desktop);
  }
}

@keyframes bhl-slide-down {
  from {
    max-height: 0;
  }

  to {
    max-height: var(--header-height-on-desktop);
  }
}

@-webkit-keyframes bhl-lift-in {
  from {
    box-shadow: 0 0 0 0 rgba(var(--swatch-alternate-color), .25), inset 0 0 0 .0625rem rgb(var(--swatch-primary-darkest));
  }

  to {
    box-shadow: .25rem .125rem .5rem 0 rgba(var(--swatch-alternate-color), .25), inset 0 0 0 .0625rem rgb(var(--swatch-primary-darkest));
  }
}

@keyframes bhl-lift-in {
  from {
    box-shadow: 0 0 0 0 rgba(var(--swatch-alternate-color), .25), inset 0 0 0 .0625rem rgb(var(--swatch-primary-darkest));
  }

  to {
    box-shadow: .25rem .125rem .5rem 0 rgba(var(--swatch-alternate-color), .25), inset 0 0 0 .0625rem rgb(var(--swatch-primary-darkest));
  }
}

